Vue.use(Vuex);


var _types = {
	'ADD_TO_CART': 1
};


const ADD_TO_CART = 'ADD_TO_CART';
const SUBTOTAL = 'SUBTOTAL';
const REMOVE_FROM_CART = 'REMOVE_FROM_CART';
const CHECKOUT_REQUEST = 'CHECKOUT_REQUEST';






export default new Vuex.Store({
	state: {
		cart: { items: [], subTotal: 0 },
		//socket: window.io('http://localhost:3000')
  	},

  	mutations: {
		[ADD_TO_CART] (state, product) {
			/*var isPresent: boolean = false;
			var items = state.cart.items.map(p => {
				if(p.id == product.id) { 
					//p = {...p, quantity: product.quantity };
					p.quantity += product.quantity;
					isPresent = true; 
					console.log('PRESENT');
					console.log(p);
				}

				console.log('NOT PRESENT');
				console.log(p);
				return p;
			});
			if(!isPresent) items.push(product);*/
			state.cart.items = [ ...state.cart.items, product];
			//state.cart.items = [ ...items];

		},

		[SUBTOTAL] (state) {
			state.cart.subTotal = state.cart.items.reduce((total, p) => total + (p.qty * p.price), 0);
		}

		  
  	},

  	actions: {
		[ADD_TO_CART]({commit, state}, payload) {
			
			commit(ADD_TO_CART, payload.product);
			/*commit({
				type: 'addToCart', product: payload.product
			});*/
			commit(SUBTOTAL);
		}
  	}
});
