		private streamStarted(data:any) {
			switch(data.val().started) {
				case "yes":{
					var off = db.VIDEO_CONNECTED.push({uuid: this.uuid});
					off.remove();
					this.streamEnd = false;
                    //this.modal.close();
				}
				break;
				case "no":{
					if(this.stream !== null){
                        this.streamEnd = true;
                        this._pause();
                        this.$forceUpdate();
						//this._pause();
						//--this.remoteVideo.pausercObject = null;
						//this.player.reset();
						//this.player.pause();
						//this.remoteVideo.src = null;
						//--this.stream = null;			
						//--this.connections = {};
                      //--if(!this.modal.opened()) this.modal.open();
					}
				}
				break;
				case "pause":{
					console.log('ause called');
                    this.player.muted(true);
					this.modalPause.open();
				}
				break;
				case "resume":{
					this.modalPause.close();
                    this.player.muted(false);
                    this._play();
				}
				break;
			}
		}

		public getRemoteMediaStream(event:RTCTrackEvent) {
			var _stream = event.streams[0];
			if(this.remoteVideo.srcObject !== _stream) this.remoteVideo.srcObject = _stream;
		}

		private getPeerConnection(uuid:string) {
			if(uuid != this.uuid){
				if(this.connections[uuid]) return this.connections[uuid];
				var pc = new RTCPeerConnection(this.servers);
				this.connections[uuid] = pc;
				pc.addEventListener("icecandidate", (e:RTCPeerConnectionIceEvent) => {
					var off = db.VIDEO_SIGNAL.push({
						d: JSON.stringify({"by": this.uuid, "to": uuid, "ice": e.candidate, "type":"ice", "admin": 0})
					});
					off.remove();		
				});
				pc.addEventListener("track", (e:RTCTrackEvent) => this.getRemoteMediaStream(e));
				return pc;
			}
		}

		private handleSignal(data:any) {
			if(this.uuid != data.by){
				var pc = this.getPeerConnection(data.by);
				switch(data.type){
					case "offer":
						pc.setRemoteDescription(data.sdp)
							.then(() => pc.createAnswer())
							.then((answer:RTCSessionDescription) =>{
								pc.setLocalDescription(answer);
								var off = db.VIDEO_SIGNAL.push({
									d: JSON.stringify({"by": this.uuid, "to": data.by, "sdp":answer, "type": "answer", "admin": 0})
								});
								off.remove();				
							});
						break;
					case "ice":
						if(data.ice) pc.addIceCandidate(data.ice);
						break;
				}
			}
		}
